import React from 'react'; //  

import styles from './order-details.module.css';
import orderAcceptedIcon from './../../images/order-accepted-icon.png';

function OrderDetails(props: { dataModal: string }) {
  return (
    <div className={styles.mainModal}>
      <p className={`text text_type_digits-large ${styles.idOrderNumber}`}>{props.dataModal}</p>
      <p className={`${styles.textOrderNumber} text text_type_main-medium`}> </p>
      <div className={styles.imageWrap}>
        <img src={orderAcceptedIcon} alt='   ' />
      </div>
      <p className={`${styles.textOrderConfirmation} text text_type_main-default `}>   </p>
      <p className={`${styles.textPleaseWait} text text_type_main-default text_color_inactive`}>    </p>
    </div>
  );
}

export default OrderDetails;
